// src/pages/ProjectsPage.jsx
import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Papa from "papaparse";
import Background from "../components/Background";
import "../main.css";
import { AnimatePresence, motion } from "framer-motion";

export default function ProjectsPage() {
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  const selectedCategory = queryParams.get("category")
    ? queryParams.get("category").toUpperCase()
    : "PERSONAL";

  const [allProjects, setAllProjects] = useState([]);

  useEffect(() => {
    fetch("/projects.csv")
      .then((res) => {
        if (!res.ok) throw new Error("Failed to fetch projects.csv");
        return res.text();
      })
      .then((csvText) => {
        Papa.parse(csvText, {
          header: true,
          skipEmptyLines: true,
          complete: ({ data }) => {
            const parsed = data.map((row) => {
              // parse the date
              let parsedDate = null;
              if (row.Date) {
                const [mon, yr] = row.Date.split("-");
                const mIdx = new Date(`${mon} 1, 2000`).getMonth();
                let y = parseInt(yr, 10);
                if (yr.length === 2) y += 2000;
                parsedDate = new Date(y, mIdx, 1);
              }

              return {
                Name: row.Name,
                Date: parsedDate,
                Category: row.Category.trim().toUpperCase(),
                Description: row.Description,
                Image: row.Image?.trim() || "",
                percentCompleted: Number(row["Percent Completed"] || 0),
                Skills: row.Skills
                  ? row.Skills.split(";").map((s) => s.trim())
                  : [],
                Features: row.Features
                  ? row.Features.split(";").map((f) => f.trim())
                  : [],
                Tasks: row.Tasks
                  ? row.Tasks.split(";").map((t) => t.trim())
                  : [],
                Link: row.Link?.trim() || "",
              };
            });
            setAllProjects(parsed);
          },
          error: (err) => console.error("CSV parse error:", err),
        });
      })
      .catch(console.error);
  }, []);

  const filtered = allProjects
    .filter((p) => p.Category === selectedCategory)
    .sort((a, b) => (a.Date && b.Date ? b.Date - a.Date : 0));

  return (
    <Background>
      <div className="page-content">
        <ul className="projects-list">
          <AnimatePresence>
            {filtered.map((proj, i) => (
              <motion.li
                key={proj.Name + i}
                className="project-item"
                initial={{ opacity: 0 }}
                animate={{
                  opacity: 1,
                  transition: { duration: 2, delay: i * 0.05 },
                }}
                exit={{ opacity: 0, transition: { duration: 0.3 } }}
              >
                {proj.Image && (
                  <div className="project-image-container">
                    {proj.Link ? (
                      <a
                        href={proj.Link}
                        target={proj.Link.startsWith("http") ? "_blank" : "_self"}
                        rel="noopener noreferrer"
                      >
                        <img
                          src={proj.Image}
                          alt={`${proj.Name} screenshot`}
                          className="project-image"
                        />
                      </a>
                    ) : (
                      <img
                        src={proj.Image}
                        alt={`${proj.Name} screenshot`}
                        className="project-image"
                      />
                    )}
                  </div>
                )}

                <div className="project-details">
                  <h3 className="project-name">
                    {proj.Link ? (
                      <a
                        href={proj.Link}
                        className="project-link"
                        target={proj.Link.startsWith("http") ? "_blank" : "_self"}
                        rel="noopener noreferrer"
                      >
                        {proj.Name}
                      </a>
                    ) : (
                      proj.Name
                    )}
                  </h3>

                  {proj.Date && (
                    <p className="project-date">
                      {proj.Date.toLocaleString(undefined, {
                        month: "short",
                        year: "numeric",
                      })}
                    </p>
                  )}

                  <p className="project-description">{proj.Description}</p>

                  {proj.Features.length > 0 && (
                    <>
                      <p className="project-features-title">Features:</p>
                      <ul className="project-features">
                        {proj.Features.map((feat, idx) => (
                          <li key={idx}>{feat}</li>
                        ))}
                      </ul>
                    </>
                  )}

                  {proj.Tasks.length > 0 && (
                    <>
                      <p className="project-tasks-title">Tasks:</p>
                      <ul className="project-tasks">
                        {proj.Tasks.map((task, idx) => (
                          <li key={idx}>{task}</li>
                        ))}
                      </ul>
                    </>
                  )}

                  <div className="project-progress">
                    <p>Progress: {proj.percentCompleted}%</p>
                    <div className="progress-bar">
                      <div
                        className="progress-bar-fill"
                        style={{ width: `${proj.percentCompleted}%` }}
                      />
                    </div>
                  </div>

                  {proj.Skills.length > 0 && (
                    <div className="project-skills">
                      {proj.Skills.map((skill, j) => (
                        <span key={j} className="skill-badge">
                          {skill}
                        </span>
                      ))}
                    </div>
                  )}
                </div>
              </motion.li>
            ))}

            {filtered.length === 0 && (
              <motion.li
                className="no-projects"
                initial={{ opacity: 0 }}
                animate={{ opacity: 1, transition: { duration: 0.6 } }}
              >
                No projects found in this category.
              </motion.li>
            )}
          </AnimatePresence>
        </ul>
      </div>
    </Background>
  );
}
