// src/components/AnimatedSectionTitle.jsx
import React, { useEffect, useRef } from 'react';
import '../main.css'; // must include .text, .fade-in, .fade-out, keyframes, etc.

export default function AnimatedSectionTitle({ sectionName, isActive }) {
  const textRef = useRef(null);
  const timeoutsRef = useRef([]);

  const DEFAULT_WORD = 'Ezekiel';
  // We’ll alternate between [“Ezekiel”, sectionName]
  const words = [DEFAULT_WORD, sectionName];

  const clearAllTimeouts = () => {
    timeoutsRef.current.forEach((tid) => clearTimeout(tid));
    timeoutsRef.current = [];
  };

  useEffect(() => {
    const textEl = textRef.current;

    // Whenever isActive becomes false or sectionName changes, clear out any existing animation:
    clearAllTimeouts();
    if (textEl) {
      textEl.innerHTML = '';
    }

    if (!isActive) {
      // If this section isn’t active right now, do nothing further.
      return;
    }

    // If active, start by showing sectionName first
    let currentIndex = 1;

    // Build a set of <span> elements for each character in `word`
    const buildSpansForWord = (word) => {
      if (!textEl) return;
      textEl.innerHTML = '';
      const fragment = document.createDocumentFragment();
      Array.from(word).forEach((ch) => {
        const span = document.createElement('span');
        span.textContent = ch === ' ' ? '\u00A0' : ch;
        fragment.appendChild(span);
      });
      textEl.appendChild(fragment);
    };

    // Fade letters in or out; `direction` is 'in' or 'out'
    const fadeLetters = (direction, onComplete) => {
      if (!textEl) return;
      const spans = Array.from(textEl.children);
      spans.forEach((span, i) => {
        span.classList.remove('fade-in', 'fade-out');
        const delayMs = i * 150; // 150ms between each letter
        const tid = setTimeout(() => {
          span.classList.add(direction === 'in' ? 'fade-in' : 'fade-out');
        }, delayMs);
        timeoutsRef.current.push(tid);
      });
      // After the last letter’s fade (delay + 0.5s animation)
      const totalSeconds = (spans.length - 1) * 0.15 + 0.5;
      const endTid = setTimeout(() => {
        onComplete && onComplete();
      }, totalSeconds * 1000);
      timeoutsRef.current.push(endTid);
    };

    // The core “toggle” logic: fade out current word → switch index → fade in → hold → repeat
    const cycleWords = () => {
      const nextIndex = currentIndex === 0 ? 1 : 0;
      fadeLetters('out', () => {
        buildSpansForWord(words[nextIndex]);
        fadeLetters('in', () => {
          // Hold for 4 seconds before toggling again
          const holdTid = setTimeout(() => {
            currentIndex = nextIndex;
            cycleWords();
          }, 4000);
          timeoutsRef.current.push(holdTid);
        });
      });
    };

    // INITIAL BUILD + FADE IN “sectionName”
    buildSpansForWord(words[1]);
    fadeLetters('in', () => {
      const initHoldTid = setTimeout(() => {
        cycleWords();
      }, 4000);
      timeoutsRef.current.push(initHoldTid);
    });

    // Cleanup on unmount or when isActive toggles
    return () => {
      clearAllTimeouts();
      if (textEl) textEl.innerHTML = '';
    };
  }, [sectionName, isActive]);

  return <div className="text" ref={textRef} />;
}
