import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Papa from "papaparse";
import Background from "../components/Background";
import "../main.css";
import { AnimatePresence, motion } from "framer-motion";

export default function ShopPage() {
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  const selectedCategory = queryParams.get("category")
    ? queryParams.get("category").toUpperCase()
    : "GUITARS";

  const [allItems, setAllItems] = useState([]);

  useEffect(() => {
    fetch("/shop.csv")
      .then((r) => {
        if (!r.ok) throw new Error("Failed to fetch shop.csv");
        return r.text();
      })
      .then((csv) =>
        Papa.parse(csv, {
          header: true,
          skipEmptyLines: true,
          complete: ({ data }) =>
            setAllItems(
              data.map((row) => ({
                Name: row.Name,
                Price: parseFloat(row.Price) || 0,
                Category: row.Category.trim().toUpperCase(),
                Description: row.Description,
                Image: row.Image?.trim()
                  ? `product-images/${row.Image.trim()}`
                  : "",
                inStock: parseInt(row["In Stock"], 10) || 0,
                Console: row.Console?.trim() || "",
                Skills: row.Skills
                  ? row.Skills.split(",").map((s) => s.trim())
                  : [],
              }))
            ),
        })
      )
      .catch(console.error);
  }, []);

  const filtered = allItems
    .filter((it) => it.Category === selectedCategory)
    .sort((a, b) => {
      if (a.inStock === 0 && b.inStock > 0) return 1;
      if (a.inStock > 0 && b.inStock === 0) return -1;
      return a.Name.localeCompare(b.Name);
    });

  return (
    <Background>
      <div className="page-content">
        <div className="category-contact">
          For NZ customers only:{" "}
          <a href="mailto:ezekielnzshop@gmail.com">
            ezekielnzshop@gmail.com
          </a>
        </div>
        <ul className="shop-list">
          <AnimatePresence>
            {filtered.map((item, i) => (
              <motion.li
                key={item.Name + i}
                className="shop-item"
                initial={{ opacity: 0 }}
                animate={{
                  opacity: 1,
                  transition: { duration: 2, delay: i * 0.05 },
                }}
                exit={{
                  opacity: 0,
                  transition: { duration: 0.3 },
                }}
              >
                {item.Image && (
                  <div className="shop-image-container">
                    <img
                      src={item.Image}
                      alt={item.Name}
                      className="shop-image"
                    />
                  </div>
                )}
                <div className="shop-details">
                  <h3 className="shop-name">{item.Name}</h3>
                  {item.inStock > 0 && (
                    <p className="shop-price">
                      ${item.Price.toFixed(2)}
                    </p>
                  )}
                  <p className="shop-description">{item.Description}</p>
                  {item.Skills.length > 0 && (
                    <div className="project-skills">
                      {item.Skills.map((skill, j) => (
                        <span key={j} className="skill-badge">
                          {skill}
                        </span>
                      ))}
                    </div>
                  )}
                  {item.Console && (
                    <div className="project-skills">
                      <span className="skill-badge">{item.Console}</span>
                    </div>
                  )}
                  <p className="shop-stock">
                    {item.inStock > 0
                      ? `In Stock: ${item.inStock}`
                      : "Out of Stock"}
                  </p>
                </div>
              </motion.li>
            ))}
            {filtered.length === 0 && (
              <motion.li
                className="no-shop-items"
                initial={{ opacity: 0 }}
                animate={{
                  opacity: 1,
                  transition: { duration: 0.6 },
                }}
              >
                No items available in this category.
              </motion.li>
            )}
          </AnimatePresence>
        </ul>
      </div>
    </Background>
  );
}
